<template>
  <!-- 对话中信 -->
  <div class="dialogue">
    <div class="zx justify-center">
      <img src="~@/assets/images/dialogue/logo@2x.png" />
    </div>
    <div class="cont">
      <div class="top justify-center">
        <img src="~@/assets/images/dialogue/box@2x.png" />
      </div>
      <div class="main">
        <div class="tips">我们很关注您的声音</div>
        <div class="write">
          <textarea
            ref="textarea"
            v-model.trim="msg"
            name="advice"
            class="advice"
            placeholder="请在此输入您想对我们说的话"
          ></textarea>
        </div>
        <div class="btns">
          <div v-show="!isShowQuery" class="btn btn1">
            <img src="~@/assets/images/dialogue/btn1@2x.png" />
          </div>
          <div v-show="!isShowQuery" class="btn btn2" @click="changeQuery">
            <!-- <img src="~@/assets/images/dialogue/btn7@2x.png" />
             -->
            留言<br />提意见
          </div>
          <div v-show="isShowQuery" class="btn btn3" @click="handleQuery">
            <img src="~@/assets/images/dialogue/btn3@2x.png" />
          </div>
        </div>
      </div>
    </div>

    <div v-show="isShowGuide" class="guide">
      <div class="mask"></div>
      <div class="content">
        <div class="btn">
          <div class="know" @click="closeGuide">知道了</div>
        </div>
        <div class="guide-img justify-center">
          <img src="~@/assets/images/dialogue/guide_bg@2x.png" />
        </div>
        <div class="tips">
          你可以向中信证券提问，也可以说出对中信证券的意见，
          每月享有3次提意见机会，成功提意见用户可得星钻奖励~
        </div>
        <div class="wrap justify-center align-center">
          <div class="btn btnl">联系客服</div>
          <div class="btn btnr">留言<br />提意见</div>
          <!-- <img src="~@/assets/images/dialogue/btn5@2x.png" /> -->
        </div>
      </div>
    </div>

    <Modal class="modal" :visible.sync="isModalShow1" title="感谢参与">
      <div class="modal-content" slot="content">
        <div class="text mb-24">赠送您{{ giveNum }}星钻奖励</div>
      </div>
      <div class="modal-footer" slot="footer">
        <img
          src="@/assets/images/common/modal_btn_zdl.png"
          @click="closeModal"
        />
      </div>
    </Modal>
  </div>
</template>

<script>

import Modal from '@/components/commonModal'

export default {
  name: 'Login',
  data () {
    return {
      isLoading: false,
      isShowGuide: !localStorage.getItem('chatGuide'),
      msg: '',
      isShowQuery: false,
      isModalShow1: false, // 成功邀请到好友
      giveNum: '' // 留言成功赠送数量

    }
  },
  components: {
    Modal
  },
  created () {
    console.log(this.isShowGuide)
    // let guideAlready = localStorage.getItem('chatGuide')
  },
  methods: {
    changeQuery () {
      this.isShowQuery = !this.isShowQuery
      this.$refs.textarea.focus()
    },
    async handleQuery () {
      if (this.msg.length === 0) {
        this.Toast(`未输入`)
        return
      }
      // 提交
      let res = await this.$request('post', 'user/sendnote', {
        uid: JSON.parse(localStorage.getItem('userInfo')).data.uid,
        message: this.msg
      })
      if (res && res.error_no === '0') {
        this.giveNum = res.data
        let timer = null
        timer = setTimeout(() => {
          this.isShowQuery = false
          this.isModalShow1 = true
          clearTimeout(timer)
          console.log(this.giveNum)
        }, 300)
      }
    },

    closeModal () {
      this.isShowQuery = false
      this.isModalShow1 = false
      this.msg = ''
    },
    closeGuide () {
      this.isShowGuide = false
      localStorage.setItem('chatGuide', 1)
    }
  }
}
</script>

<style lang="less" scoped>
@import url("../assets/css/mixin.less");
.dialogue {
  width: 100%;
  height: 100%;
  background-color: #fc3623;
  padding: 20px 0 55px;
  .bg-image("~@/assets/images/dialogue/bg","png");
  .zx {
    width: 100%;
    height: 424px;

    > img {
      width: 440px;
      height: 424px;
    }
  }
  .cont {
    margin-top: -50px;
    position: relative;
    .top {
      width: 100%;
      height: 100%;
      > img {
        width: 675px;
        height: 885px;
      }
    }
    .main {
      width: 100%;
      position: absolute;
      bottom: 20px;
      padding: 0 92px;
      .tips {
        width: 100%;
        height: 40px;
        font-size: 30px;
        color: #97251b;
        text-align: center;
      }
      .write {
        width: 100%;
        padding: 0 50px;
        > textarea {
          border: none;
          width: 100%;
          height: 90px;
          overflow: auto;
          background: transparent;
          font-size: 30px;
          color: #97251b;
          text-align: left;
          padding: 20px;
          outline: none;
        }
      }
      .btns {
        width: 100%;
        height: 210px;
        .flex_func(row,center,center,nowrap);
        .btn {
          width: 190px;
          height: 110px;
          .flex_func(row,center,center,nowrap);
          text-align: center;
        }
        .btn1 {
          margin-right: 50px;
        }
        .btn2 {
          .bg-image("~@/assets/images/dialogue/btnr","png");
          font-size: 34px;
          color: #ffffdf;
        }
      }
    }
  }
  .guide {
    z-index: 4;
    .mask {
      position: fixed;
      z-index: 5;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      height: 100vh;
      width: 100vw;
      background-color: rgba(0, 0, 0, 0.8);
    }
    .content {
      width: 100%;
      height: 100%;
      position: fixed;
      bottom: 0;
      padding-bottom: 110px;
      z-index: 6;
      .flex_func(column,flex-end,center,nowrap);
      .btn {
        width: 100%;
        height: 100px;
        .know {
          width: 240px;
          margin: 0 auto;
          height: 70px;
          border: 2px solid #fff;
          border-radius: 35px;
          text-align: center;
          font-size: 28px;
          line-height: 70px;
          color: #fff;
        }
      }
      .guide-img {
        width: 100%;
        height: 150px;
        margin-top: 50px;
        > img {
          width: 362px;
          height: 150px;
        }
      }
      .tips {
        width: 100%;
        height: 120px;
        padding: 0 90px;
        font-size: 22px;
        color: #fffeff;
        line-height: 45px;
      }
      .wrap {
        width: calc(100% - 180px);
        margin: 0 auto;
        height: 173px;
        border: 2px dashed #fff;
        border-radius: 20px;
        text-align: center;
        > img {
          width: 506px;
          height: 146px;
        }
        .btn {
          width: 220px;
          height: 146px;
          font-size: 34px;
          color: #ffffdf;
          .bg-image("~@/assets/images/dialogue/btn5","png");
          .flex_func(row,center,center,nowrap);
        }
        .btnl {
        }
      }
    }
  }
}
</style>



